<template>
  <div id="main">
    <swiper
      :slides-per-view="1"
      :space-between="50"
      direction="vertical"
      :mousewheel="true"
      :modules="modules"
      :pagination="{ clickable: true, renderBullet: renderBullet }"
      @swiper="onSwiper"
      @slideChange="onSlideChange"
    >
      <swiper-slide>
        <home-page></home-page>
      </swiper-slide>
      <swiper-slide>Slide 2</swiper-slide>
      <swiper-slide>Slide 3</swiper-slide>
      <swiper-slide>Slide 1</swiper-slide>
      <swiper-slide>Slide 2</swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { Mousewheel, Pagination, A11y } from 'swiper';
import { Swiper, SwiperSlide } from "swiper/vue";
import 'swiper/css';
import 'swiper/css/pagination';

import HomePage from "./compoments/home-page/home-page"
export default {
  name: 'App',
  components: {
    Swiper,
    SwiperSlide,
    HomePage
  },
  setup() {
    const onSwiper = (swiper) => {
      console.log(swiper);
    };
    const onSlideChange = () => {
      console.log('slide change');
    };
    const renderBullet = (index, className) => {
      let text;
      switch(index){
        case 0:text='壹';break;
        case 1:text='贰';break;
        case 2:text='叁';break;
        case 3:text='肆';break;
        case 4:text='伍';break;
      }
      return '<span class="' + className + '">' + text + '</span>';
    };
    return {
      onSwiper,
      onSlideChange,
      renderBullet,
      modules: [Mousewheel, Pagination, A11y]
    };
  },
}
</script>
<style lang="scss">
  @import "style/index.scss";
</style>
